
import React, {Component, createRef} from 'react';
import './App.css';
// import userPic from './img/userPic.png';
import {Motion, spring, presets} from 'react-motion'; // 5.7k
// import { Spring, animated, Trail } from 'react-spring'; // 12.2 k
import {ActionButton} from './comp/expo';

class Spring extends Component {
  state = {
    moved: false,
    imgUrl: ''
  };
  circle = createRef();
  // METHODS
  _onClick = () => {
    // call api -> store the image url in state
    fetch('https://randomuser.me/api/?inc=picture&noinfo')
      .then(res => {
        res.json()
      .then(json => {
        let user = json.results[0];
        this.setState({
          imgUrl: user.picture.large
        });
      })
      })
      .catch(err => console.log(err))
      this.setState({moved: !this.state.moved});
  }

  componentDidMount() {
    this.circle.current.style.transform = 'translate3d(0)';
    this.circle.current.addEventListener('click', this._onClick);
  }
  componentWillUnmount() {
    this.circle.current.style.transform = 'translate(none)';
  }
  render() {
    return (
      <div className="container">

        <Motion
          defaultStyle={{opacity: 0, y: 30}}
          style={{
            opacity: spring(1),
            y: spring(this.state.moved ? 30 : 0, {damping: 8, stiffness: 130}),}}>
          
          {springs => (
            <div>
              <div
                ref={this.circle}
                className="ball"
                style={{
                  opacity: springs.opacity,
                  transform: `translate3d(0, ${springs.y}px, 0)`,
                }}>
                <img width="100%" height="100%" src={!this.state.imgUrl ? '' : this.state.imgUrl} alt="Profile Picture"/>
              </div>
            </div>
          )}
        </Motion>

      </div>
    );
  }
}

class App extends Component {
  state = {
    checked: false
  }
  _switchChange = (e) => {
    this.setState({
      checked: !this.state.checked
    });
  }
  render() {
    return (
      <div className="container">
        <label className="switch" onChange={this._switchChange}>
          <input type="checkbox"/>
          <span className="slider__2 round"></span>
          <span className="slider__1 round"></span>
          <Switch on={this.state.checked}/>
        </label>
      </div>
    );
  }
}

const Switch = ({on}) => (
  <Motion
    defaultStyle={{x: 0}}
    style={{x: spring(on ? 26 : 0, {damping: 16, stiffness: 220})}}>
      {interpolatedStyle => <div style={{transform: `translateX(${interpolatedStyle.x}px)`}} className="circle"></div>}
  </Motion>
);
// <div className="container"><ActionButton text="Click me!"/></div>
export default App;


